.btn-toggle {
  z-index: 4;
  height: 24px;
  cursor: pointer;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  flex-direction: column;
  transition: transform .1s ease-out;
  display: flex;
  position: relative;
  transform: translateZ(0);
}

.btn-toggle:active {
  transform: translateY(4px);
}

.btn-toggle:focus .line:after, .btn-toggle:hover .line:after, .btn-toggle:active .line:after {
  background-color: #0f00e0;
}

.line {
  width: 32px;
  padding: 2px;
  display: block;
}

.line:after {
  content: "";
  width: 100%;
  height: 4px;
  transform: translateZ(0) rotate(0);
  background-color: #0029ff;
  border-radius: 2px;
  transition: background-color .2s ease-out;
  display: block;
}

.open .line:nth-child(1) {
  animation: jump-1 .9s forwards;
}

.open .line:nth-child(1):after {
  animation: line-1 .9s ease-in-out forwards;
}

.open .line:nth-child(2) {
  animation: jump-2 .9s forwards;
}

.open .line:nth-child(2):after {
  animation: line-2 .9s ease-in-out forwards;
}

.close .line:nth-child(1) {
  animation: jump-1 .9s reverse;
}

.close .line:nth-child(1):after {
  animation: line-1 .9s ease-in-out reverse;
}

.close .line:nth-child(2) {
  animation: jump-2 .9s reverse;
}

.close .line:nth-child(2):after {
  animation: line-2 .9s ease-in-out reverse;
}

.open .line:nth-child(3), .close .line:nth-child(3) {
  animation: jump-3 .9s ease-out forwards;
}

@keyframes line-1 {
  10% {
    transform: translateZ(0) rotate(0);
  }

  80% {
    transform: translateZ(0)rotate(395deg);
  }

  90%, 100% {
    transform: translateZ(0)rotate(405deg);
  }
}

@keyframes line-2 {
  10% {
    transform: translateZ(0) rotate(0);
  }

  20% {
    transform: translateZ(0)rotate(10deg);
  }

  90%, 100% {
    transform: translateZ(0)rotate(-405deg);
  }
}

@keyframes jump-1 {
  10% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-48px);
  }

  90%, 100% {
    transform: translateY(-4px);
  }
}

@keyframes jump-2 {
  10% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-40px);
  }

  85%, 100% {
    transform: translateY(-12px);
  }
}

@keyframes jump-3 {
  10% {
    transform: translateY(-4px)rotate(15deg);
  }

  30% {
    transform: translateY(-16px)rotate(-10deg);
  }

  50% {
    transform: translateY(4px)rotate(5deg);
  }

  80% {
    transform: translateY(0);
  }
}

@keyframes glow {
  50% {
    box-shadow: 0 0 2px 2px rgba(51, 84, 255, .4);
  }
}

